


























































































































































import { Component, Vue } from "vue-property-decorator";
import { Swipe, SwipeItem } from "vant";
import { getSwipeList, getRecommendList } from "@/api/index.ts";
Vue.use(Swipe);
Vue.use(SwipeItem);

@Component
export default class HomePage extends Vue {
  swipeArr = []; // 
  scrollTop = 0; // 
  recommend = {}; // 

  // 
  handleScroll() {
    this.scrollTop = (this.$refs.homepage as HTMLElement).scrollTop;
    if (this.scrollTop >= 30) {
      (this.$refs.searchBoxOuter as HTMLElement).classList.add("searchBox-top");
    } else {
      (this.$refs.searchBoxOuter as HTMLElement).classList.remove(
        "searchBox-top"
      );
    }
  }
  handleTouchstart() {
    (this.$refs.redPackets as HTMLElement).classList.add("bg-opacity");
  }
  handleTouchend() {
    (this.$refs.redPackets as HTMLElement).classList.remove("bg-opacity");
  }

  // computed: {};
  created() {
    getSwipeList()
      .then((res: any) => {
        console.log(res);
        if (res.data.responsecode && res.data.responsecode === "1") {
          this.swipeArr = res.data.response.list;
        }
      })
      .catch((err: any) => {
        console.log(err);
      });

    getRecommendList()
      .then((res: any) => {
        console.log(res);
        if (res.data.responsecode && res.data.responsecode === "1") {
          this.recommend = res.data.response;
        }
      })
      .catch((err: any) => {
        console.log(err);
      });
  }
  mounted() {
    (this.$refs.homepage as HTMLElement).addEventListener(
      "scroll",
      this.handleScroll,
      true
    );

    (this.$refs.homepage as HTMLElement).addEventListener(
      "touchstart",
      this.handleTouchstart,
      false
    );
    (this.$refs.homepage as HTMLElement).addEventListener(
      "touchend",
      this.handleTouchend,
      false
    );
  }
  destroyed() {
    // 
    window.removeEventListener("scroll", this.handleScroll);
  }
}
