import axios from "axios";
import router from "../router";
import { Toast } from "vant";
// axios
const service: any = axios.create({
  timeout: 10000 // 
});
let key1 = false;
let key2 = false;
// request()
service.interceptors.request.use(
  (config: any) => {
    Toast.loading({
      mask: false,
      duration: 0,
      forbidClick: true,
      message: ""
    });
    setTimeout(() => {
      key2 = true;
      if (key1) {
        Toast.clear();
        key1 = false;
        key2 = false;
      }
    }, 500);
    return config;
  },
  (error: any) => {
    Toast.clear();
    Toast("!");
    Promise.reject(error);
  }
);
// respone()
service.interceptors.response.use(
  (response: any) => {
    key1 = true;
    if (key2) {
      Toast.clear();
      key1 = false;
      key2 = false;
    }
    let res: any = {};
    res.data = response.data;
    return res;
  },
  (error: any) => {
    Toast.clear();
    Toast("!");
    if (error.response && error.response.status == 404) {
      router.push("/blank.vue");
    }
    return Promise.reject(error.response);
  }
);
//export
export default service;
